@use 'base/variables' as v;
@use 'base/mixins' as m;

.destacada{
    margin-top: 5rem;
    background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, v.$primario 50%), url(../img/destacada.jpg);
    background-repeat: no-repeat;
    background-size: 100%, cover;
    background-position: center, -60rem -30rem;

    @include m.tablet { 
        background-image: linear-gradient(to right, transparent 0%, transparent 50%, v.$primario 50%), url(../img/destacada.jpg);
        background-position: center, -45rem center;
    }

    &__grid{
        display: grid;
        grid-template-rows: repeat(2, 1fr);

        @include m.tablet { 
            grid-template-rows: unset;
            @include m.grid(2, 2rem);
        }
    }

    &__contenido{
        grid-row: 2/3;
        padding: 2rem;

        @include m.tablet { 
            grid-row: unset;
            grid-column: 2/3;
            padding: 10rem 5rem;
        }
    }

    &__heading{
        color: v.$blanco;
        text-transform: uppercase;
    }

    &__texto{
        color: v.$blanco;
        font-size: 1.8rem;
    }

    &__precio{
        color: v.$blanco;
        font-size: 2.8rem;
        font-weight: 700;
        margin: 0
    }

    &__boton{
        @include m.boton(v.$blanco, v.$primario);
    }
}