@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimoniales-blog{
    margin-top: 5rem;

    @include m.desktop { 
        @include m.grid(3, 4rem);
    }

    &__heading{
        @include m.heading(true);
    }

    &__blog{
        grid-column: 2/4;
    }
}

.testimonial{
    &__imagen{
        width: 20rem;
        margin: 0 auto;
    }

    &__contenido{
        background-color: v.$blanco;
        margin-top: -10rem;
        padding: 10rem 2rem 2rem 2rem;
    }

    &__nombre{
        font-size: 2.3rem;
        font-weight: 700;
        text-align: center;
    }

    &__texto{
        text-align: center;

        &::before{
            content: '';
            background-image: url(../img/quote.svg);
            background-repeat: no-repeat;
            background-position: center;
            display: inline-block;
            width: 3rem;
            height: 3rem;
            margin-right: .5rem;
        }
    }
}

.entrada{
    background-color: v.$blanco;
    margin-bottom: 2rem;

    @include m.desktop { 
        @include m.grid(5,1rem);
        align-items: center;
    }

    @include m.desktop { 
        &__imagen-contenedor{
            grid-column: 1/3;
        }
    }

    &__imagen{
        width: 100%;
        height: 30rem;
        object-fit: cover;
    }

    &__contenido{
        padding: 2rem 0 2rem 2rem;

        @include m.desktop { 
            grid-column: 3/6;
        }
    }

    &__meta{
        font-size: 1.3rem;
    }

    &__texto{
        font-size: 1.8rem;
        margin-top: 0;
    }

    &__boton{
        @include m.boton(v.$primario, v.$blanco);
        margin-top: 0;
        display: block;
        @include m.tablet { 
            display: inline-block;
        }
    }
}