@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background-image: linear-gradient(to bottom, transparentize(v.$primario, .2) 0%, transparentize(v.$primario, .2) 100%), url(../img/header_bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 3rem 0;
    position: relative;

    @include m.tablet { 
        padding: 3rem 0 20rem 0;
    }

    &__barra{
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet { 
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__logo{
        width: 25rem;
    }

    &__contenido{
        margin-top: 10rem;
        max-width: 50rem;
    }

    &__heading{
        color: v.$blanco;
        text-align: center;
        @include m.tablet { 
            text-align: left;
        }
    }
    
    &__parrafo{
        display: none;
        color: v.$blanco;
        @include m.tablet { 
            display: block;
        }
    }

    &__imagen{
        display: none;
        @include m.tablet { 
            display: block;
            position: absolute;
            right: 0;
            bottom: -5rem;
            max-width: 35rem;
            border-bottom-left-radius: 3rem;
            border-top-left-radius: 1rem;
        }
        @include m.desktopXL { 
            max-width: 50rem;
        }
    }
}

.navegacion{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    margin-top: 4rem;

    &__enlace{
        color: v.$blanco;
        font-size: 1.8rem;
        transition: transform .2s ease;

        &:hover{
            transform: scale(1.1);
        }
    }

    @include m.tablet { 
        flex-direction: row;
        margin-top: 0;
    }
}