@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    background-color: v.$primario;
    padding: 5rem 0 0 0;

    &__grid{
        @include m.tablet { 
            @include m.grid(3, 4rem);
        }
    }

    &__box{
        margin-top: 2rem;
        @include m.tablet { 
            margin-top: 0;
        }
    }

    &__heading{
        color: v.$blanco;
    }

    
    &__navegacion{
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }

    &__texto,&__enlace{
        color: v.$blanco;
    }

    &__enlace{
        &:hover{
            color: darken(v.$blanco, 20%);
        }
    }

    &__logo{
        max-width: 30rem;
    }
}

.copyright{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .5rem;
    padding: .6rem;
    background-color: darken(v.$primario, 10%);
    margin-top: 2rem;
    color: v.$blanco;
    

    &__texto{
        color: v.$blanco;
        font-size: 2rem;
        text-transform: uppercase;
        font-weight: 700;
        margin: 0;

        span{
            font-size: 1.3rem;
            text-transform: none;
        }
    }

    &__icono{
        transition: transform .2s ease;

        &:hover{
            transform: scale(1.2);
        }
    }
}