@use 'base/variables' as v;
@use 'base/mixins' as m;

.propiedades{
    margin-top: 5rem;
    
    @include m.tablet { 
        margin-top: 10rem;
    }

    &__heading{
        @include m.heading;
    }

    &__grid{
        @include m.grid(1, 2rem);

        @include m.tablet { 
            @include m.grid(3, 2rem);
        }
    }
}

.propiedad{
    background-color: v.$blanco;

    &__imagen{
        aspect-ratio: 10/6;
        background-size: auto 150%;
        position: relative;
    }

    @for $i from 1 through 6 {
        $imagen: '../img/propiedad_' + $i + '.jpg';

        &:nth-child(#{$i}) &__imagen{
            background-image: linear-gradient(to bottom, transparentize(v.$negro, 1%) 0%, transparentize(v.$negro, .3%) 100%), url($imagen);
        }
    }

    &__contenido{
        padding: 2rem;
    }

    &__nombre{
        color: v.$blanco;
        position: absolute;
        bottom: .5rem;
        left: .5rem;
        font-size: 2.3rem;
    }

    &__descripcion{
        margin-bottom: 0;
    }

    &__precio{
        font-size: 3rem;
        font-weight: 700;
        margin: 0;
    }

    &__boton{
        @include m.boton(v.$primario, v.$blanco);
        display: block;
    }
}